@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri&display=swap);
body,
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.properties {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.list__pokemon {
  display: flex;
  justify-content: space-between;
}

.list__pokemon_img {
  width: 100px;
  height: 100px;
}

.list__pokemon_name {
  padding-top: 35px;
}

.pagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 20px;
}

.pagination a {
  display: block;
  margin: 0 5px;
}

.list__wrapper {
  padding: 0 20px;
  font-family: "Hind Siliguri", sans-serif;
  text-transform: uppercase;
}

.list__loadButton {
  font-family: "Hind Siliguri", sans-serif;
}



